<template>
    <div class="stepOne">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="headTitle">教练认证</span>
            <span class="back"></span>
        </div>
        <div class="coachInfo">
            <div class="coachName">
                <label>姓名</label>
                <input type="text" v-model="user.name">
            </div>
            <div class="idNum">
                <label>身份证号</label>
                <input type="text" maxlength="18" v-model="user.idNum">
            </div>
            <button class="nextBtn" @click="submit">下一步</button>
        </div>
    </div>
</template>

<script>
import { uploadParam } from '../../api/getdata'
import Qs from 'qs'
import { Toast } from 'mint-ui'
export default {
  name: 'coachStep1',
  data () {
    return {
      userType: '',
      user: {
        name: '', idNum: ''
      }
    }
  },
  /* mounted () {
    this.userType = getCookie('userType')
  }, */
  methods: {
    back () {
      this.$router.go(-1)
    },
    submit () {
      let idCardReg = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/
      // console.log(this.user.idNum.length)
      // console.log(idCardReg.test(this.user.idNum))
      if (idCardReg.test(this.user.idNum) && this.user.name) {
        // console.log(this.user.name)
        let data = uploadParam(Qs.stringify({
          'name': this.user.name, 'idenTityNo': this.user.idNum
        }))
        data.then(res => {
          // console.log(res)
          if (res.status === '0') {
            Toast({
              message: '提交成功',
              duration: 1000
            })
            this.$router.push('/home/enroll/coachStep2')
          } else {
            Toast({
              message: '提交失败，请重新提交',
              duration: 1000
            })
          }
        })
      } else {
        Toast({
          message: '输入格式不正确',
          duration: 1000
        })
      }
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.5rem;
  }
  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .back img {
    width: 100%;
  }

  .coachInfo {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-top: 5.493333rem;
  }

  .coachInfo label {
    display: inline-block;
    width: 2rem;
  }

  .coachName,
  .idNum {
    display: flex;
    justify-content: space-between;
    padding: 0.2rem;
  }

  .coachInfo input {
    width: calc(100% - 2.4rem);
    border: none;
    border-bottom: 1px solid #666666;
    outline: none;
  }

  .nextBtn {
    width: 100%;
    height: 1.33333rem;
    border-radius: 0.15rem;
    background: #ffd200;
    border: none;
    outline: none;
    margin-top: 4.213333rem;
    font-weight: 400;
  }
</style>



